import {IActionModel, ActionType} from '../../../directives/action/models/action.model';
import {MethodType} from '../../../directives/action/models/interface.model';

const ip = "https://172.16.140.164";

class Login {
    static key: string = "saltApiLogin";

    constructor() {
        let actionModel: IActionModel = {
            key: Login.key,
            type: ActionType.form,
            title: "登陆",
            icon: "login",
            form: {
                dataSchema: {
                    type: "object",
                    required: ["username", "password"],
                    properties: {
                        username: {
                            type: "string",
                            minLength: 4,
                            maxLength: 20,
                            title: "用户名"
                        },
                        eauth: {
                            type: "string",
                            default: "pam"
                        },
                        password: {
                            type: "string",
                            title: "密码",
                            minLength: 4,
                            maxLength: 20
                        }
                    }
                },
                formSchema: [{
                    key: "username",
                    type: "string",
                    placeHolder: "用户名",
                    icon: {
                        leftIcon: "account"
                    },
                    htmlClass: "md-icon-left md-block"
                }, {
                    key: "password",
                    type: "password",
                    icon: {
                        leftIcon: "lock"
                    },
                    htmlClass: "md-icon-left md-block"
                }]
            },
            refreshList: true,
            closeDialog: true,
            interfaces: [{
                key: "saltApiLogin",
                method: MethodType.POST,
                address: ip,
                port: 8888,
                path: "login",
                jpp: {
                    set: {
                        "/data": "/return"
                    }
                },
                header: {
                    set: {
                        "/X-Auth-Token": "/return/0/token"
                    }
                },
                isRestful: false
            }]
        };

        return actionModel;
    }
}

class Logout {
    static key: string = "saltApiLogout";

    constructor() {
        let actionModel: IActionModel = {
            key: Logout.key,
            type: ActionType.confirm,
            title: "salt退出登录",
            icon: "logout",
            refreshList: true,
            confirm: {
                confirmTitle: "",
                confirmContent: "确定要退出登录吗!"
            },
            interfaces: [{
                key: "saltApiLogin",
                method: MethodType.POST,
                address: ip,
                port: 8888,
                path: "logout",
                jpp: {
                    set: {
                        "/data": "/return"
                    }
                },
                isRestful: false
            }]
        };

        return actionModel;
    }
}

class Minions {
    static key: string = "saltApiMinions";

    constructor() {
        let actionModel: IActionModel = {
            key: Minions.key,
            type: ActionType.confirm,
            title: "所有CLIENT",
            icon: "client",
            refreshList: true,
            confirm: {
                confirmTitle: "",
                confirmContent: "获取minions!"
            },
            interfaces: [{
                key: "saltApiMinions",
                method: MethodType.GET,
                address: ip,
                port: 8888,
                path: "minions",
                jpp: {
                    set: {
                        "/data": "/return"
                    }
                },
                isRestful: false
            }]
        };

        return actionModel;
    }
}

class Jobs {
    static key: string = "saltApiMinions";

    constructor() {
        let actionModel: IActionModel = {
            key: Jobs.key,
            type: ActionType.confirm,
            title: "所有CLIENT",
            icon: "client",
            refreshList: true,
            confirm: {
                confirmTitle: "",
                confirmContent: "获取minions!"
            },
            interfaces: [{
                key: "saltApiJobs",
                method: MethodType.GET,
                address: ip,
                port: 8888,
                path: "jobs",
                jpp: {
                    set: {
                        "/data": "/return"
                    }
                },
                isRestful: false
            }]
        };

        return actionModel;
    }
}

export default (module: ng.IModule)=> {
    const services = [Login, Logout, Minions];

    _.each(services, (model)=> {
        module.service(model.key, model);
    });
}



